const Key_ExportFile = async () => {
  const MakeBlob = async function() {
    const key = await NewKey()
    const exported = await window.crypto.subtle.exportKey("jwk", key)    
    console.log(`expoted: `, exported)
    const exportedKeyBuffer = new Uint8Array(exported)
    console.log(`expobuff: `, exportedKeyBuffer)
    return JSON.stringify(exported)
  }
  const NewKey = async function() {
    const key = await window.crypto.subtle.generateKey(
      {
        name: "AES-GCM",
        length: 256,
      },
      true,
      ["encrypt", "decrypt"],
    )
    return key
  }

  return await MakeBlob()
}

export default Key_ExportFile;